import React, { Component } from "react";
import Aux from "../../hoc/Auxillary";
import Burger from "../../components/Burger/Burger";
import BuildControls from "../../components/Burger/BuildControls/BuildControls";
import Modal from "../../UI/Modal/Modal";
import OrderSummary from "../../components/Burger/OrderSummary/OrderSummary";
import Spinner from "../../UI/Spinner/Spinner";
import withErrorHandler from "../../hoc/withErrorHandler/withErrorHandler";
import axios from "../../axios-order";
import { connect } from "react-redux";
import * as actions from "../../store/action/index";

class BurgerBuilder extends Component {
  state = {
    purchasing: false,
    loading: false
  };
  componentDidMount() {
    //moved to redux
    // axios
    //   .get("https://react-my-burger-d7735.firebaseio.com/ingredients.json")
    //   .then(response => {
    //     this.setState({ ingredients: response.data });
    //   })
    //   .catch(error => {
    //     this.setState({ error: true });
    //   });

    this.props.onInitIngredients();
  }
  updatePurchasableState = ingredients => {
    const sum = Object.keys(ingredients)
      .map(igKey => {
        return ingredients[igKey];
      })
      .reduce((sum, el) => {
        return sum + el;
      }, 0);

    return sum > 0;
  };
  purchasingHandler = () => {
    if (this.props.isAuthenticated) {
      this.setState({ purchasing: true });
    } else {
      this.props.history.push("/auth");
      this.props.onSetAuthRedirectPath("/checkout");
    }
  };
  cancelPurshasing = () => {
    this.setState({ purchasing: false });
  };
  continuePurchasing = () => {
    //not required fetched from redux no need of query params
    //alert('You Continued');
    // const queryParams = [];
    // for (let i in this.props.ings) {
    //   queryParams.push(
    //     encodeURIComponent(i) + "=" + encodeURIComponent(this.props.ings[i])
    //   );
    // }
    // queryParams.push("price=" + this.props.totalPrice);
    // const queryString = queryParams.join("&");
    // this.props.history.push({
    //   pathname: "/checkout",
    //   search: "?" + queryString
    // });
    this.props.onPurchaseInit();
    this.props.history.push("/checkout");
  };

  render() {
    const disabledInfo = {
      ...this.props.ings
    };
    for (let key in disabledInfo) {
      if (disabledInfo[key] == 0) disabledInfo[key] = true;
      else disabledInfo[key] = false;
    }

    let orderSummary = null;
    let burger = this.props.error ? (
      <p>Ingredients can't be loaded</p>
    ) : (
      <Spinner />
    );

    if (this.props.ings) {
      burger = (
        <Aux>
          <Burger ingredients={this.props.ings} />
          <BuildControls
            addIngredient={this.props.onIngredientAdded}
            removeIngredient={this.props.onIngredientRemoved}
            disabled={disabledInfo}
            price={this.props.totalPrice}
            ordered={this.purchasingHandler}
            isAuthenticated={this.props.isAuthenticated}
            purchasable={this.updatePurchasableState(this.props.ings)}
          />
        </Aux>
      );
      orderSummary = (
        <OrderSummary
          ingredients={this.props.ings}
          cancelPurshasing={this.cancelPurshasing}
          continuePurchasing={this.continuePurchasing}
          price={this.props.totalPrice}
        />
      );
    }
    if (this.state.loading) {
      orderSummary = <Spinner />;
    }
    return (
      <Aux>
        <Modal
          show={this.state.purchasing}
          clickedOutsideModal={this.cancelPurshasing}
        >
          {orderSummary}
        </Modal>
        {burger}
      </Aux>
    );
  }
}

const mapStateToProps = state => {
  return {
    ings: state.burgerBuilder.ingredients,
    totalPrice: state.burgerBuilder.totalPrice,
    error: state.burgerBuilder.error,
    isAuthenticated: state.auth.idToken != null
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onIngredientAdded: igName => {
      dispatch(actions.addIngredient(igName));
    },
    onIngredientRemoved: igName => {
      dispatch(actions.removeIngredient(igName));
    },
    onInitIngredients: () => {
      dispatch(actions.initIngredients());
    },
    onPurchaseInit: () => {
      dispatch(actions.purchaseInit());
    },
    onSetAuthRedirectPath: path => {
      dispatch(actions.authRedirect(path));
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withErrorHandler(BurgerBuilder, axios));

