import React from "react";
import BurgerLogo from "../../assests/img/burger-logo.png";
import "./Logo.css";

const logo = props => {
  return (
    <div className="Logo" style={{ height: props.height }}>
      <img src={BurgerLogo} alt="My Burger" />
    </div>
  );
};
export default logo;



