import React, { Component } from "react";
import Button from "../../../UI/Button/Button";
import axios from "../../../axios-order";
import "./ContactData.css";
import Spinner from "../../../UI/Spinner/Spinner";
import Input from "../../../UI/Input/Input";
import { connect } from "react-redux";
import withErrorHandler from "../../../hoc/withErrorHandler/withErrorHandler";
import * as orderActions from "../../../store/action/index";

class ContactData extends Component {
  state = {
    orderForm: {
      name: this.orderFormInitialiser("input", "text", "Your Name", "", {
        required: true
      }),
      city: this.orderFormInitialiser("input", "text", "Your City", "", {
        required: true
      }),
      country: this.orderFormInitialiser("input", "text", "Your Country", "", {
        required: true
      }),
      zipCode: this.orderFormInitialiser(
        "input",
        "number",
        "Your Zipcode",
        "",
        { required: true, minLength: 6, maxLength: 6 }
      ),
      email: this.orderFormInitialiser("input", "email", "Your Mail", "", {
        required: true
      }),
      deliveryMethod: {
        elementType: "select",
        elementConfig: {
          options: [
            { value: "fastest", displayValue: "Fastest" },
            { value: "cheapest", displayValue: "Cheapest" }
          ]
        },
        validation: {},
        valid: true,
        value: "fastest",
        validationErrorMsg: null
      }
    },
    loading: false,
    isFormValid: false
  };
  orderFormInitialiser(elementType, type, placeholder, value, validationRules) {
    let formType = {
      elementType: elementType,
      elementConfig: {
        type: type,
        placeholder: placeholder
      },
      validation: validationRules,
      valid: false,
      value: value,
      touched: false,
      validationErrorMsg: null
    };
    return formType;
  }
  orderHandler = event => {
    event.preventDefault();
    const formData = {};
    for (let formElementIdentifier in this.state.orderForm) {
      formData[formElementIdentifier] = this.state.orderForm[
        formElementIdentifier
      ].value;
    }
    this.setState({ loading: true });
    const order = {
      ingredients: this.props.ings,
      price: this.props.totalPrice,
      customerData: formData,
      userId: this.props.userId
    };
    //console.log("dfg");

    this.props.onOrderBurger(order, this.props.token);

    // moved to redux
    // axios
    //   .post("/orders.json", order)
    //   .then(response => {
    //     this.setState({ loading: false });
    //     //console.log(this.props.history + "fhfghfghfgh");
    //     this.props.history.push("/");
    //   })
    //   .catch(error => {
    //     this.setState({ loading: false });
    //   });
  };

  checkValidity = (value, rules) => {
    let isValid = true;
    let validationErrorMsg = [];
    if (rules.required) {
      isValid = value.trim() !== "" && isValid;
      // if (!isValid) {
      //   validationErrorMsg.push("This field can't be empty!!");
      // }
    }

    if (rules.minLength && isValid) {
      isValid = value.length >= rules.minLength && isValid;
      // if (!isValid) {
      //   validationErrorMsg.push(
      //     "The minimum length should be greater than" + rules.minLength
      //   );
      // }
    }

    if (rules.maxLength && isValid) {
      isValid = value.length <= rules.maxLength && isValid;
      // if (!isValid) {
      //   validationErrorMsg.push(
      //     "The minimum length should be less than" + rules.maxLength
      //   );
      // }
    }

    return isValid;
  };

  inputChangeHandler = (event, inputIdentifier) => {
    const updatedForm = {
      ...this.state.orderForm
    };
    const updatedFormElememt = {
      ...updatedForm[inputIdentifier]
    };
    updatedFormElememt.value = event.target.value;
    updatedFormElememt.valid = this.checkValidity(
      updatedFormElememt.value,
      updatedFormElememt.validation
    );
    updatedFormElememt.touched = true;
    updatedForm[inputIdentifier] = updatedFormElememt;

    let isFormValid = true;
    for (let inputIdentifier in updatedForm) {
      isFormValid = updatedForm[inputIdentifier].valid && isFormValid;
    }
    this.setState({ orderForm: updatedForm, isFormValid: isFormValid });
    //console.log(updatedFormElememt);
  };
  render() {
    let formElementsArray = [];
    for (let key in this.state.orderForm) {
      formElementsArray.push({
        id: key,
        config: this.state.orderForm[key]
      });
    }
    //console.log(formElementsArray);

    let form = (
      <form onSubmit={this.orderHandler}>
        {formElementsArray.map(formElement => (
          <Input
            key={formElement.id}
            elementType={formElement.config.elementType}
            elementConfig={formElement.config.elementConfig}
            value={formElement.config.value}
            inValid={!formElement.config.valid}
            shouldValidate={formElement.config.validation}
            touched={formElement.config.touched}
            changed={event => this.inputChangeHandler(event, formElement.id)}
          />
        ))}

        <Button buttonType="Success" disabled={!this.state.isFormValid}>
          ORDER
        </Button>
      </form>
    );
    if (this.props.loading) {
      form = <Spinner />;
    }
    return (
      <div className="ContactData">
        <h3>Enter your contact details...</h3>
        {form}
      </div>
    );
  }
}
const mapStateToProps = state => {
  return {
    ings: state.burgerBuilder.ingredients,
    totalPrice: state.burgerBuilder.totalPrice,
    loading: state.order.loading,
    token: state.auth.idToken,
    userId: state.auth.userId
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onOrderBurger: (order, token) => {
      dispatch(orderActions.purchaseBurger(order, token));
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withErrorHandler(ContactData, axios));



