import React, { Component } from "react";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";
import Button from "../../UI/Button/Button";
import Input from "../../UI/Input/Input";
import Spinner from "../../UI/Spinner/Spinner";
import "./Auth.css";
import * as actions from "../../store/action/index";
class Auth extends Component {
  state = {
    controls: {
      email: {
        elementType: "input",
        elementConfig: {
          type: "text",
          placeholder: "Your Email"
        },
        validation: {
          required: true,
          isEmail: true
        },
        valid: false,
        value: "",
        touched: false
      },
      password: {
        elementType: "input",
        elementConfig: {
          type: "password",
          placeholder: "Your Password"
        },
        validation: {
          required: true,
          minLength: 6
        },
        valid: false,
        value: "",
        touched: false
      }
    },
    isSignup: false
  };

  componentDidMount() {
    if (!this.props.buildingBurger && this.props.authRedirectPath !== "/") {
      this.props.onSetAuthRedirectPath("/");
    }
  }
  checkValidity = (value, rules) => {
    let isValid = true;
    if (!rules) {
      return true;
    }
    if (rules.required) {
      isValid = value.trim() !== "" && isValid;
    }

    if (rules.minLength && isValid) {
      isValid = value.length >= rules.minLength && isValid;
    }

    if (rules.maxLength && isValid) {
      isValid = value.length <= rules.maxLength && isValid;
    }
    return isValid;
  };
  inputChangeHandler = (event, inputIdentifier) => {
    const updatedControls = {
      ...this.state.controls
    };
    const focusedControl = {
      ...updatedControls[inputIdentifier]
    };
    focusedControl.value = event.target.value;
    focusedControl.touched = true;
    focusedControl.valid = this.checkValidity(
      focusedControl.value,
      focusedControl.validation
    );

    updatedControls[inputIdentifier] = focusedControl;

    let isFormValid = true;
    for (let inputIdentifier in updatedControls) {
      isFormValid = updatedControls[inputIdentifier].valid && isFormValid;
    }
    this.setState({ controls: updatedControls, isFormValid: isFormValid });
  };
  submitHandler = event => {
    event.preventDefault();
    this.props.onSubmitAuthForm(
      this.state.controls.email.value,
      this.state.controls.password.value,
      this.state.isSignup
    );
  };
  switchFormHandler = () => {
    this.setState(prevState => {
      return { isSignup: !prevState.isSignup };
    });
  };
  render() {
    let formControlArray = [];
    for (let key in this.state.controls) {
      formControlArray.push({
        id: key,
        config: this.state.controls[key]
      });
    }
    let errorMessage = null;
    if (this.props.error) {
      errorMessage = <p>{this.props.error.message}</p>;
    }
    let form = <Spinner />;

    let authRediret = null;
    if (this.props.isAuthenticated) {
      //console.log(this.props.authRedirectPath);
      authRediret = <Redirect to={this.props.authRedirectPath} />;
    }

    if (!this.props.loading) {
      form = (
        <div>
          <p style={{ color: "#703B09" }}>
            {this.state.isSignup ? "SIGN UP" : "SIGN IN"}
          </p>
          <form onSubmit={this.submitHandler}>
            {formControlArray.map(formElement => (
              <Input
                key={formElement.id}
                elementType={formElement.config.elementType}
                elementConfig={formElement.config.elementConfig}
                value={formElement.config.value}
                inValid={!formElement.config.valid}
                shouldValidate={formElement.config.validation}
                touched={formElement.config.touched}
                changed={event =>
                  this.inputChangeHandler(event, formElement.id)
                }
              />
            ))}
            <Button buttonType="Success" disabled={!this.state.isFormValid}>
              SUBMIT
            </Button>
          </form>
          <Button buttonType="Danger" clicked={this.switchFormHandler}>
            SWITCH TO {this.state.isSignup ? "SIGNIN" : "SIGNUP"}
          </Button>
        </div>
      );
    }
    return (
      <div className="Auth">
        {authRediret}
        {errorMessage}
        {form}
      </div>
    );
  }
}
const mapStateToProps = state => {
  return {
    loading: state.auth.loading,
    error: state.auth.error,
    isAuthenticated: state.auth.idToken != null,
    authRedirectPath: state.auth.authRedirectPath,
    buildingBurger: state.burgerBuilder.buildingBurger
  };
};
const mapDispatchToProps = dispatch => {
  return {
    onSubmitAuthForm: (email, password, isSignup) => {
      dispatch(actions.auth(email, password, isSignup));
    },
    onSetAuthRedirectPath: path => {
      dispatch(actions.authRedirect(path));
    }
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(Auth);



